.login-popup
  position: fixed
  min-height: 100vh
  top: 50%
  transform: translateY(-50%)
  opacity: 0
  width: 100%
  margin: 0 auto
  z-index: 5
  visibility: hidden
  display: flex
  justify-content: center
  align-items: center

  &__overlay
    width: 100%
    height: 100%
    background-color: rgba(#000,0.3)
    position: absolute
    top: 0
    left: 0

  &__container
    width: 45%
    position: relative

  &_opened
    opacity: 1
    visibility: visible

  &__close-button
    color: #fff
    width: 50px
    height: 50px
    position: absolute
    top: -40px
    right: -40px
    font-size: 30px
    background-color: transparent
    outline: 0
    border: 0
    transition: all 0.2s cubic-bezier(0, 1, 1, 1)

    &:hover
      opacity: 0.7

@media screen and ( max-width: 1280px )

@media screen and ( max-width: 1024px )

@media screen and ( max-width: 768px )
  .login-popup
    &__container
      width: 100%
    &__close-button
      top: -45px
      right: 0
